@import '../base.scss';

.c-back-button__btn {
  margin: 1.875rem $s-gutter--mobile;
  max-width: 125rem;


  @include t-media-query($s-small-up) {
    margin: 2.5rem $s-gutter--small;
  }

  @include t-media-query($s-medium-up) {
    margin: 3.125rem $s-gutter--medium;
  }
}